header {
	width: 350px;
	height: 100%;
	position: fixed;
	display: flex;
	flex-flow: column nowrap;
}

header h1 {
	width: 100%;
	height: 100px;
	padding: 30px 0;
	/* border: 1px solid #fff; */
	display: flex;
	justify-content: center;
	align-items: center;
}

header h1 a {
	color: #fff;
	font-family: "Qwitcher Grypen", cursive;
	font-weight: 600;
	font-style: normal;
	font-size: 80px;
}
